@import url('https://fonts.googleapis.com/css?family=Roboto');

body{
  font-family: 'Roboto', sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
i {
  margin-right: 10px;
}

/*------------------------*/
input:focus,
button:focus,
.form-control:focus{
  outline: none;
  box-shadow: none;
}
.form-control:disabled, .form-control[readonly]{
  background-color: #fff;
}
#myTable input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.price{
  color: black !important;
}
.table tr,
.table tr td{
  vertical-align: middle;
}
.button-container{
  display: flex;
  align-items: center;
}
.button-container .form-control{
  max-width: 80px;
  text-align: center;
  display: inline-block;
  margin: 0px 5px;
}
#myTable .form-control{
  width: auto;
  display: inline-block;
}
.cart-qty-plus,
.cart-qty-minus{
  width: 38px;
  height: 38px;
  background-color: #fff;
  border: 1px solid #ced4da;
    border-radius: .25rem;
}
.cart-qty-plus:hover,
.cart-qty-minus:hover{
  background-color: #53b5aa;
  color: #fff;
}
.img-prdct{
  width: 50px;
  height: 50px;
  background-color: #53b5aa;
  border-radius: 4px;
}
.img-prdct img{
  width: 100%;
}